  @Prop() private names!: string
  //data
  private txt: string = ''
  // private sum: number = 0
  //computed
  get getTxt(){
    return this.txt
  }
  //methods
  @Emit('clickSearch')
  private clickSearch(){
    // this.sum++
    console.log(`sum : ${this.txt}`)
    return this.txt
  }
  //生命周期
  created(){
    // console.log('created')
  }
  //watch
  @Watch('txt') 
  changeTxt(newTxt: string, oldTxt: string){
    // console.log(`change txt: ${oldTxt} to ${newTxt}`)
  }
  
  
}
